import { data } from "../data";

export class UIController {
    domElements = {
        navigationTrigger: document.getElementById('navigationTrigger'),
        navigationList: document.getElementById('navigation'),
        menuList: document.querySelector('.grid--menu'),
        numberOfAdultInput: document.getElementById('numberOfAdult'),
        numberOfChildInput: document.getElementById('numberOfChild'),
        date: document.getElementById('date'),
        alcoholList: document.querySelector('.pricing'),
        totalCost: document.getElementById('totalCost'),
        costPerPerson: document.getElementById('costPerPerson'),
        costPerChild: document.getElementById('costPerChild'),
        summaryList: document.querySelector('.summary__list'),
        openModal: document.getElementById('openModal'),
        closeModal: document.getElementById('closeModal'),
        modal: document.querySelector('.modal'),
        summaryValue: document.getElementById('summaryValue'),
        dateSummary: document.getElementById('dateSummary'),
        persons: document.getElementById('persons'),
        childs: document.getElementById('childs'),
        total: document.getElementById('total'),
        perperson: document.getElementById('perperson'),
        summary: document.getElementById('summary'),
        summaryTrigger: document.querySelector('.summary-trigger'),
    }

    getDomElements = () => this.domElements;

    updateDate = (date) => {
        (<HTMLInputElement>this.domElements.date).value = date;
    }

    updateAdultNumber = (number) => {
        (<HTMLInputElement>this.domElements.numberOfAdultInput).value = number;
    }

    updateChildNumber = (number) => {
        (<HTMLInputElement>this.domElements.numberOfChildInput).value = number;
    }

    updateTotalCost = (number) => {
        this.domElements.totalCost.textContent = number.toFixed(0).toLocaleString();
    }

    updateAdultCost = (number) => {
        this.domElements.costPerPerson.textContent = number.toFixed(0).toLocaleString() + ' PLN';
    }

    updateChildCost = (number) => {
        this.domElements.costPerChild.textContent = number.toFixed(0).toLocaleString() + ' PLN';
    }

    updateSummary = (html) => {
        this.domElements.summaryList.innerHTML = '';
        this.domElements.summaryList.insertAdjacentHTML('afterbegin', html);
    }

    setButtonsColor = (summary) => {

        const allBtns = document.querySelectorAll('.btn--add');

        allBtns.forEach(element => {

            const id = (<HTMLElement>element).dataset.id.trim();

            const name = data.find(item => item.id === id).name;

            (<HTMLElement>element).style.background = 'transparent';

            summary.forEach(el => {
                const elName = el.name;

                if(elName === name) {
                    (<HTMLElement>element).style.background = 'rgb(216, 229, 214)';
                }

            })

        });
    }

}