import { action, autorun, computed, decorate, observable, toJS } from 'mobx';
import { data } from './data';
import { start } from 'repl';

export class Store {

    uiCtrl;
    startItem;
    state;


    constructor(uiController, initState) {
        this.uiCtrl = uiController;
        this.state = initState;
    }

    /* FUNCTIONS */

    calculatePriceRatio(item) {

        const price = item.price / (this.state.adultPersons + this.state.childPersons);
        const adultTax = 1 + (1 - item.childDiscount);
        return {
            adult: price * adultTax,
            child: price * item.childDiscount
        }
    }

    /* COMPUTED */
    
    get getState() {
        return toJS(this.state);
    };

    get getDate() {
        return this.state.date;
    }

    get getAdultNumber() {
        return this.state.adultPersons;
    }

    get getChildNumber() {
        return this.state.childPersons;
    }

    get getAdultCost() {
        return this.state.menuItems.reduce((acc, next) => {
            if(!next.perPerson) {
                return acc + (this.calculatePriceRatio(next).adult * this.state.adultPersons);
            }

            return acc + (next.price * this.state.adultPersons);
        }, 0);
    }
    
    get getChildCost() {     
        return this.state.menuItems.reduce((acc, next) => {
            if(!next.perPerson) {
                return acc + (this.calculatePriceRatio(next).child * this.state.childPersons);
            }
    
            return acc + ((next.price * next.childDiscount) * this.state.childPersons);
        }, 0);
    }

    get getTotalCost() {
        return this.getAdultCost + this.getChildCost;
    }

    /* ACTIONS */

    updateDate = (date: string) => {
        this.state.date = date;
        this.updateUI();
    }
    
    updateAdultNumber = (number: number) => {
        this.state.adultPersons = number;
        this.updateUI();
    }

    updateChildNumber = (number: number) => {
        this.state.childPersons = number;
        this.updateUI();
    }

    deleteItem = (id) => {
        const index = this.state.menuItems.findIndex(item => item.id === id);

        if(index > 0) {
            this.state.menuItems.splice(index, 1);
            this.updateUI();
        }
    }

    addItem = (id) => {
        const newItem = data.find(item => {
            return item.id === id;
        });

        if(newItem) {
            this.state.menuItems.push(<any>newItem);
        }

        this.updateUI();
    }

    toggleItem = (id) => {
        const item = this.state.menuItems.find(item => item.id === id); 

        if(item) {
            this.deleteItem(id);
        } else {
            this.addItem(id);
        }
    }

    addAlcohol = (id, quantity: number) => {

        const index = this.state.menuItems.findIndex(item => item.id === id);

        if(index > -1) {
            const id = this.state.menuItems[index].id;
            this.deleteItem(id);
        }

        const item = data.find(item => item.id === id);
        const newItem = {
            ...item,
            price: item.price * quantity
        }
        
        this.state.menuItems.push(<any>newItem);

        this.updateUI();
    } 

    generateSummaryHTML() {
        const adult = this.state.menuItems.reduce((acc, next) => {

            const unitPrice = next.perPerson ? next.price : this.calculatePriceRatio(next).adult;

            const price = next.perPerson ? (
                next.price * this.state.adultPersons
            ) : (
                this.calculatePriceRatio(next).adult * this.state.adultPersons
            );

            return acc + `<li>
                            <p>${next.name}</p>
                            <p>${unitPrice.toFixed(0)}</p>
                            <p>${price.toFixed(0)}</p>
                            <button class='btn btn--delete' data-id='${next.id}'>x</button>
                        </li>`;
        }, '');

        const child = this.state.menuItems.reduce((acc, next) => {

            const unitPrice = next.perPerson ? (next.price * next.childDiscount) : this.calculatePriceRatio(next).child;

            const price = next.perPerson ? (
                (next.price * next.childDiscount) * this.state.childPersons
            ) : (
                this.calculatePriceRatio(next).child * this.state.childPersons
            );

            return acc + `<li>
                            <p>${next.name} - dzieci</p>
                            <p>${unitPrice.toFixed(0) }</p>
                            <p>${price.toFixed(0)}</p>
                            <button class='btn btn--delete' data-id='${next.id}'>x</button>
                        </li>`;
        }, '')

        return adult + child;
    }

    updateUI() {
        this.uiCtrl.updateDate(this.getDate);
        this.uiCtrl.updateAdultNumber(this.getAdultNumber);
        this.uiCtrl.updateChildNumber(this.getChildNumber);
        this.uiCtrl.updateTotalCost(this.getTotalCost);
        this.uiCtrl.updateAdultCost(this.getAdultCost / this.state.adultPersons);

        const childCost = isNaN(this.getChildCost / this.state.childPersons) ? 0 : this.getChildCost / this.state.childPersons;

        this.uiCtrl.updateChildCost(childCost);
        this.uiCtrl.updateSummary(this.generateSummaryHTML());
        this.uiCtrl.setButtonsColor(this.state.menuItems);

    }

    saveData = () => {
        try {
            localStorage.setItem('weddings', JSON.stringify(this.state));
        } catch (error) {
            console.log(error);
        }
    }

    run = autorun(() => {
        this.saveData();
    });
}

decorate(Store, {
    state: observable,
    getState: computed,
    getDate: computed,
    getAdultNumber: computed,
    getChildNumber: computed,
    getAdultCost: computed,
    getTotalCost: computed,
    updateDate: action,
    updateAdultNumber: action,
    updateChildNumber: action,
    deleteItem: action,
    addItem: action,
});