
class Modal {

  modalContainer: HTMLDivElement;
  modalImage: HTMLImageElement;

  constructor() {
    this.setupDOM();
    this.setupListeners();
  }

  setupDOM = () => {
    this.modalContainer = document.createElement("div");
    this.modalImage = document.createElement("img");
    this.modalImage.alt = "";
    this.modalContainer.id = "modal";
    this.modalContainer.classList.add("modalimg");
    this.modalContainer.appendChild(this.modalImage);
    document.body.appendChild(this.modalContainer);
  }
  
  setupListeners = () => {
    this.modalContainer.addEventListener('click', event => {
      if((<HTMLDivElement>event.target).tagName === 'DIV') {
        this.closeModal();
      }
    });

    document.addEventListener("click", event => {
      if ((<HTMLImageElement>event.target).tagName === 'IMG') {
        const src = (<HTMLImageElement>event.target).src;
        this.openModal(src);
      }
    });
  }

  openModal = (src: string) => {
    this.modalImage.src = src;
    this.modalContainer.classList.add("open");
  }

  closeModal= () => {
    this.modalContainer.classList.remove("open");
  }
}

export default Modal;