import { Store } from './store';
import { UIController } from './controllers/uiController';
import Modal from './modal';
import { data } from './data';
 
const uiCtrl = new UIController();

let startItem;

if(document.getElementById('menu-selector')) {

    let startItem = document.getElementById('menu-selector').dataset.menu;

}

let INIT_STATE = {};

if(startItem) {
    INIT_STATE = {
            adultPersons: 60,
            childPersons: 0,
            date: '',
            menuItems: [data.find(item => item.id === startItem)]
    }
} else {
    INIT_STATE = {
        adultPersons: 60,
        childPersons: 0,
        date: '',
        menuItems: [data.find(item => item.id === 'MENU1')]
    }
}

const localData = localStorage.getItem('weddings');
const continueOrder = sessionStorage.getItem('weddings_continue');

if(sessionStorage.getItem('weddings_continue') || confirm('Kontynuować poprzednie zamówienie?')) {
    try {

        sessionStorage.setItem('weddings_continue', <any>true);
        const storageItem = JSON.parse(localStorage.getItem('weddings'));

        if(storageItem === null) {
            INIT_STATE = INIT_STATE;
        } else {
            INIT_STATE = {...INIT_STATE, ...storageItem}
        }
    } catch(e) {
        console.log(e);
    }
} else {
    localStorage.removeItem('weddings');
    sessionStorage.setItem('weddings_continue', <any>true);
}


const store = new Store(uiCtrl, INIT_STATE);

class AppController {

    store;
    uiCtrl;
    domElements;

    constructor(storage, uiCtrl) {
        this.store = storage;
        this.uiCtrl = uiCtrl;
        this.domElements = uiCtrl.getDomElements();
    }

    setupListeners() {

        this.domElements.date.addEventListener('blur', event => {
            const date = event.target.value;
            this.store.updateDate(date);
        });

        this.domElements.numberOfAdultInput.addEventListener('blur', event => {
            const number = parseInt(event.target.value);
            this.store.updateAdultNumber(number);
        });

        this.domElements.numberOfChildInput.addEventListener('blur', event => {
            const number = parseInt(event.target.value);
            this.store.updateChildNumber(number);
        });

        document.addEventListener('click', event => {
            if((<HTMLElement>event.target).className === 'btn btn--delete') {
                const id = (<HTMLElement>event.target).dataset.id;
                this.store.deleteItem(id);
            }
        });

        document.addEventListener('click', event => {
            if((<HTMLElement>event.target).className === 'btn btn--add') {
                const id = (<HTMLElement>event.target).dataset.id;
                this.store.toggleItem(id);                
            }
        });

        if(this.domElements.alcoholList) {
            this.domElements.alcoholList.addEventListener('change', event => {
                const id = (<HTMLElement>event.target).dataset.id;
                const quantity = parseInt((<HTMLInputElement>event.target).value);

                this.store.addAlcohol(id, quantity);
            })
        }

    }

    init() {
        this.setupListeners();
        this.store.updateUI();
    }
}

const app = new AppController(store, uiCtrl);

if(document.querySelector('.menu')) {
    
    app.init();

}


const modal = new Modal();

document.getElementById('navigationTrigger').addEventListener('click', () => {
    document.getElementById('navigation').classList.toggle('navigation--is-open')
});

document.querySelector('.summary-trigger').addEventListener('click', () => {
    document.querySelector('.summary').classList.toggle('summary--is-open');
});